a button{cursor:pointer;}


.App {
  text-align: center;
}
nav,div{box-sizing: border-box;}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

.App__black-nav{
  display: flex;
  align-items: center;
  background: #000;
  color:#fff;
  padding-left: 20px;
}

.list{
  padding:0px 20px 20px; 
  text-align: left;
  border-bottom:1px solid #ccc; cursor:pointer;}
.list__title{display:flex;  align-items: center;justify-content: space-between;margin:0;}
.list__like{margin:0 1rem;}
.list__like-btn{display:inline-block;transition:all .3s; margin:0 .5rem;}
.list__like-btn:hover{transform:rotate(-5deg) scale(1.1);}
.list__title, .list__date{margin:0;}
.modal{
  margin-top : 20px;
  padding : 20px;
  background : #eee;
  text-align : left;
}




@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}
